.container{
    text-align: center;
}

.title{
    margin-top: 15px;
    color: #F6F4EB;
    font-size: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title span{
    color: rgb(227, 165, 71);
    padding-left: 15px;
    padding-right: 15px;
}

.reset{
    background: #F6F4EB;
    width: 8.5rem;
    height: 3rem;
    border-radius: 30px;
    border: #F6F4EB;
    color: #4682A9;
    font-size: 25px;
    cursor: pointer;
    opacity: 0.6;
}
.reset:hover{
    opacity: 1;
}

.boxes{
    display: flex;
    height: 180px;
    width: 180px;
    justify-content: center;
    background: #91C8E4;
    border: 8px solid #749BC2;
    cursor: pointer;
}

.board{
    height: 600px;
    width: 590px;
    display: flex;
    margin: auto;
}

.title img{
    padding: 20px;
    width: 50px;
    height: 50px;
    opacity: 0.8;
}

  body::-webkit-scrollbar {
    display: none;
  }